body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  font-family: "Verdana", cursive, sans-serif;
  color: #ffffff;
}

body {
  background-color: #2196f3;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.app {
  background-color: #252d4a;
  width: 540px;
  /* min-height: 200px;
  height: min-content; */
  max-height: 300px;
  overflow: auto;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  /* display: flex; */
  justify-content: space-evenly;
}
.app::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.app::-webkit-scrollbar-track {
  background: blue; 
}
 
/* Handle */
.app::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
.app::-webkit-scrollbar-thumb:hover {
  background: rgb(236, 234, 234); 
}
.score-section {
  display: flex;
  font-size: 24px;
  align-items: center;
  margin-bottom: 30px;
  justify-content: center;

}

/* QUESTION/TIMER/LEFT SECTION */
.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
}

.timer-text {
  background: rgb(230, 153, 12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255, 189, 67);
  border-radius: 15px;
  text-align: center;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 5px solid #2196f39c;
  cursor: pointer;
  margin-bottom: 5px;

}

.correct {
  background-color: #2f922f;
}

.incorrect {
  background-color: #ff3333;
}

button:hover {
  background-color: #555e7d;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}

.score-question{
  font-size: 13px;
    line-height: 31px;
    list-style: lower-roman;
}

li{
  color: #03a9f4;
    font-size: 20px;
    list-style: initial;
}

.auth-form{
  width: fit-content;
  display: flex;
  flex-direction: column;
  line-height: 50px;
  padding: 20px;
  background: white;
  min-width: 300px;
}


.form-field{
  margin-left: 20px;
  color: black;
}

.redirect{
  display: flex;

}

.textfield{
  color: red;
}

.MuiFormControl-root.MuiTextField-root{
margin-bottom: 20px;
}

.redirect{

   color: black;
    align-items: center;
    margin-top: -5px;
}

.redirect-button{
  color: black;
  padding-left: 17px;
  align-items: center;
  border: 1px solid cornflowerblue;
  border-radius: 19px;
  width: 99px;
  height: 40px;
  margin-top: 6px;
  background: cadetblue;
  margin-left: 20px;
}

.redirect-button ::after{
  background-color: cornflowerblue !important  ;
  color: white;
}

.register-button{
  
  background: none;
  border: 1px solid cornflowerblue;
  width: 120px;
  color: black;
  height: 39px;
  border-radius: 4px;
  padding-left: 27px;
  margin: auto;
}

.meter {
  height: 20px;
  position: relative;
  background: #d9dce5;
  border-radius: 25px;
  box-shadow: inset 0 -1px 1px rgb(255 255 255 / 30%);
  /* width: 100%; */
}

.meter > span {
  border-radius: 25px;
    display: block;
    height: 100%;
    background-color: #a140ff;
    background-image: linear-gradient(to bottom, #7dc9df, #a140ff);
    box-shadow: inset 0 2px 9px rgb(255 255 255 / 30%), inset 0 -2px 6px rgb(0 0 0 / 40%);
    position: relative;
    overflow: hidden;
}
